:root{
    --primary: #fff;
}

.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}
.btn--primary{
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);

}
.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;

}
.btn--medium{
    padding: 8px 20px;
    font-size: 18px;
}
.btn--large{
    padding: 12px 26px;
    font-size: 20px;

}
.btn--mobile{
    text-align: center;
    border-radius: 4px;
    width: 100%;
    text-decoration: none;
    font-size: 20px;
    background-color: transparent;
    color: #fff;
    padding: 14px 24px;
    transition: all 0.3s ease-out;
}
.btn--wide{
    padding: 12px 64px;
    font-size: 20px;
}

.btn--large:hover, .btn--medium:hover, .btn--obile:hover{
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
}
.btn--wide:hover{
    color: #fff;
    background-color: red;
    transition: all 0.2s ease-out;
}
.blue{
    background-color: blue;
    color: #fff;
    border: none;
}
.red{
    background-color: rgb(255, 0, 0);
    color: #fff;
    border: none;
}
.primary{
    background-color: #242424;
    color: #fff;
    border: none;
}
.primary:hover{
    background-color: #fff;
    color: #242424;
    border: none;
}
.green{
    background-color: #04ff4f;
    color: #fff;
    border: none;
}
.green:hover{
    background-color: #242424;
}